.Sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 250px;
  height: 100vh;
  background-color: #009587;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.menu {
  color: white;
  font-size: 28px;
}

.menu-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}

.menu-tag {
  width: 100%;
  margin: 10px 0;
  padding: 10px 0;
  font-size: 30px;
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
}

.menu-tag:hover {
  background-color: #00BCD3;
}

.active-tag {
  background-color: #00BCD3;
}

h2 {
  text-align: center;
  margin: 5px;
}


